* { margin: 0; padding: 0; line-height: 1; box-sizing: border-box; }
html, body { position: relative; height: 100%; width: 100%; min-height: 100vh; min-width: 320px;}
body { overflow-x: hidden; font-family: Tahoma; font-size: 16px;}
h1 {margin: 0 0 10px 0; font-size: 2em;}
h2 {margin: 0 0 20px 0; font-size: 1em;}

#root {width: 100%; height: 100%; display: flex; justify-content: center;}
.main {text-align: center; padding: 30px;}
.search {margin: 25px 0;}
.result {margin: 0 auto; width: 100%; max-width: 800px;}
.result p {font-size: 16px; line-height: 1.4; color: #333; margin: 10px 0;}
.result img {display: block; margin: 0 auto; max-width: 215px; max-height: 300px; border: 1px solid #b7b7b7;}